
.about-us-all {


    header {
        height: 1034px;

        nav {
            @extend %NotFirstHeader;
        }

        .info-block {
            @include InfoBlock (60.3%, 600px, 65%);

            .title {
                @include Title(Roboro-Medium, 48px);
            }     

            .information {
                font-family: Roboro-Regular;
            }

            .icon-container {
                justify-content: space-between;

                .icons-info {
                    width: fit-content;
                    .facts {
                        margin-left: 3px;
                    }
                }
            }
        }
    }

    .map-info {
        flex-direction: column;
        
        .title {
            @include Title(Roboro-Regular, 46px);
            p {
                margin-top: 77px;
                margin-bottom: 0;
            }
        }

        img {
            margin-top: 46px;
            margin-bottom: 70px;
        }

        .grey-border {
            border-radius: 10px;
            border: 1px solid $BorderGreyColor;
            width: 60%;
            font-size: 20px;
            font-family: Roboro-light;
            margin-bottom: 81px;

            p {
                width: 84.3%;
                margin-top: 42px;
                margin-bottom: 42px;
            }
        }
    }

    .grey-background.grid-wrapper {
        padding: 87px 0;
    }

    .grid-img-info {
        width: 79.8%;
        display: grid;
        grid-template-columns: repeat(2, 47.7%);
        column-gap: 62px;
        row-gap: 52px;

        .text-item {
            @include GreenBorderBox (100%, 416px);
            
            .title {
                @include Title(Roboro-Regular, 28px);
                p{
                    margin-top: 41px;
                    margin-bottom: 19px;
                }
            }

            .gorizontal-line {
                @include GorizontalLineGreen (220px);
            }

            .information {
            font-family: Roboro-light;
            font-size: 18px;
            text-align: left;
            width: 85%;
            }
        }

        .photo-container {
            width: 100%;
            height: 416px;
            img {
                width: 100%;
                height: 100%;
            }
        }
    }

    .title.grey-background {
        @include Title(Roboro-Regular, 64px);
        text-align: center;
        padding-bottom: 62px;
        p{
            margin: 0;
        }
    }

    .production-abilities{
        padding: 88px 0;
        
        .abilities {
            width: 77.7%;
            gap: 40px;

            .photo {
                border-radius: 10px;
            }

            .icon-container {
                flex-direction: column;
                justify-content: flex-start;
                
                .icons-info{
                    border: none;
                    width: 100%;
                    justify-content: flex-start
                }
            }

            .divider{
                @include GorizontalLineGreen (1px);
                height: 177px;
            }

            .information {
                font-size: 18px;
                font-family: Roboro-Light;
                p{
                    margin: 0;
                }
            }
        }
    }

}